webpackJsonp([140201364841985],{

/***/ 791:
/***/ (function(module, exports) {

	module.exports = {"data":{"site":{"siteMetadata":{"title":"rhostem.github.io","url":"https://rhostem.github.io","author":"rhostem","description":"프론트엔드 웹 개발 기술 블로그"}},"markdownRemark":{"html":"<p>Gregg Pollack의 <a href=\"https://medium.com/vue-mastery/the-best-explanation-of-javascript-reactivity-fea6112dd80d\" target=\"_blank\" rel=\"nofollow\">The Best Explanation of JavaScript Reactivity 🎆</a>를 번역한 글이다.</p>\n<p>SPA 프레임워크에서 컴포넌트의 상태가 변경되면 렌더링이 다시 실행되는 것처럼, 객체의 속성이 변경될 때 어떤 코드가 자동으로 실행되게 한다면 그 어플리케이션은 반응적이라고 할 수 있다. 이를 구현하기 위해서 <code class=\"language-text\">Object.defineProperty</code> 함수를 사용해 getter, setter 함수를 다시 할당하는 방법을 설명하는 글이다.</p>\n<hr>\n<p>많은 프론트엔드 자바스크립트 프레임워크(Angular, React, Vue, etc)는 자신만의 반응성(Reactivity) 엔진을 가지고 있다. 반응성이 무엇인지, 어떻게 동작하는지를 이해함으로써 당신의 개발 기술을 향상할 수 있고 자바스크립트 프레임워크를 보다 효율적으로 사용할 수 있다. 아래의 영상과 비디오를 통해 우리는 Vue 소스 코드에서 확인할 수 있는 것과 같은 종류의 Reactivity를 구현할 것이다.</p>\n<div class=\"iframe-video-wrapper\">\n  <iframe class=\"iframe-video video\" src=\"https://www.youtube.com/embed/7Cjb7Xj8fEI\" frameborder=\"0\" scrolling=\"no\" allow=\"autoplay; encrypted-media\" allowfullscreen></iframe>\n</div>\n<p>글을 읽는 대신 비디오를 시청하고 싶다면 <a href=\"https://www.vuemastery.com/courses/advanced-componentscode/evan-you-on-proxies/\" target=\"_blank\" rel=\"nofollow\">이 시리즈의 다음 영상</a>을 보면서 Vue를 만든 Evan You와 함께 반응성과 프록시에 대해 토론하는 내용을 확인하길 바란다.</p>\n<h2 id=\"반응성-시스템\"><a href=\"#%EB%B0%98%EC%9D%91%EC%84%B1-%EC%8B%9C%EC%8A%A4%ED%85%9C\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>반응성 시스템</h2>\n<p>Vue의 반응성 시스템을 처음 보면 마치 마법처럼 느껴질 수 있다. 간단한 Vue 앱을 확인해보자.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/code_1-1e98edcdf8b0f1786ae8106dee4db305-3d7b4.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 21.25%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAAECAIAAAABPYjBAAAACXBIWXMAAAsSAAALEgHS3X78AAAAd0lEQVQI123O0Q7DIAgF0P6IIu3MlDlMUZPSrP//W1vX+NJ4Hm64DwQmAPCRX1mqrPxOP0QUYwiRfCB3QgC0I5O15pmE17bvKiKlFFVtrW2bfo6j1sqcY27z8jDG3Jcdog8J58V1+HcNV4JzFmB4+c50vZ05fPsL6JQhNNmclHcAAAAASUVORK5CYII='); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px #ffffff;\"\n        alt=\"code_1.png\"\n        title=\"\"\n        src=\"/static/code_1-1e98edcdf8b0f1786ae8106dee4db305-3d7b4.png\"\n        srcset=\"/static/code_1-1e98edcdf8b0f1786ae8106dee4db305-7e884.png 265w,\n/static/code_1-1e98edcdf8b0f1786ae8106dee4db305-9c901.png 530w,\n/static/code_1-1e98edcdf8b0f1786ae8106dee4db305-3d7b4.png 800w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/code_2-eaa93034a97818a5a77ce6196d44b0dd-3d7b4.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 51.75000000000001%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px #ffffff;\"\n        alt=\"code_2.png\"\n        title=\"\"\n        src=\"/static/code_2-eaa93034a97818a5a77ce6196d44b0dd-3d7b4.png\"\n        srcset=\"/static/code_2-eaa93034a97818a5a77ce6196d44b0dd-7e884.png 265w,\n/static/code_2-eaa93034a97818a5a77ce6196d44b0dd-9c901.png 530w,\n/static/code_2-eaa93034a97818a5a77ce6196d44b0dd-3d7b4.png 800w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>Vue가 만약 <code class=\"language-text\">price</code> 값이 변했다는 사실을 알게 된다면 아래 3가지 일을 할 것이다.</p>\n<ul>\n<li>웹페이지의 <code class=\"language-text\">price</code> 값을 업데이트한다.</li>\n<li><code class=\"language-text\">price * quantity</code> 를 다시 계산하고 페이지를 업데이트한다.</li>\n<li><code class=\"language-text\">totalPriceWithTax</code> 함수를 다시 호출하고 페이지를 업데이트한다.</li>\n</ul>\n<p>하지만 잠깐, 당신은 이런 의문을 가지게 될 것이다. Vue는 <code class=\"language-text\">price</code>가 변경되면 <strong>무엇을</strong> 업데이트해야 하는지 어떻게 알며, <strong>어떻게</strong> 모든 것을 추적하고 있는가?</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/code_3-ef80df73ca043d57f305cd5aaa594e08-1c06c.jpeg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 55.99999999999999%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px #ffffff;\"\n        alt=\"code_3.jpeg\"\n        title=\"\"\n        src=\"/static/code_3-ef80df73ca043d57f305cd5aaa594e08-1c06c.jpeg\"\n        srcset=\"/static/code_3-ef80df73ca043d57f305cd5aaa594e08-9b303.jpeg 265w,\n/static/code_3-ef80df73ca043d57f305cd5aaa594e08-e3f78.jpeg 530w,\n/static/code_3-ef80df73ca043d57f305cd5aaa594e08-1c06c.jpeg 800w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p><strong>Vue가 하는 일은 자바스크립트가 일반적으로 동작하는 방식이 아니다.</strong></p>\n<p>다시 말해, 우리가 의문을 가지고 있는 문제는 일반적인 프로그램 동작 방식이 아니라는 말이다. 예를 들어 만약 아래의 코드를 실행한다면:</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/code_4-744de5f8b6deebd17ff0de9506d5d6ee-3d7b4.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 21.375000000000004%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAAECAIAAAABPYjBAAAACXBIWXMAAAsSAAALEgHS3X78AAAAeklEQVQI122OSwoEIQxEvYiaj5Foa4swiPc/2aR7YFY+QpFNUc8BSdK+955zXlcVSYjIzEQUDQgRo1044Tjle37WWnOO8qKqY4xaKyJQAsrISuFUd0ikd0tFe++apQgLo/Vba5a27X0IPpyXn4ivIMCTv99M0XTPtn++CuEgQCq9+jEAAAAASUVORK5CYII='); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px #ffffff;\"\n        alt=\"code_4.png\"\n        title=\"\"\n        src=\"/static/code_4-744de5f8b6deebd17ff0de9506d5d6ee-3d7b4.png\"\n        srcset=\"/static/code_4-744de5f8b6deebd17ff0de9506d5d6ee-7e884.png 265w,\n/static/code_4-744de5f8b6deebd17ff0de9506d5d6ee-9c901.png 530w,\n/static/code_4-744de5f8b6deebd17ff0de9506d5d6ee-3d7b4.png 800w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>무엇이 출력될 것으로 생각하는가? 보통의 자바스크립트 코드라서 <code class=\"language-text\">10</code>을 출력할 것이다.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/code_5-c383a812e466d5c48235ea70673e1418-3d7b4.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 9%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAACCAIAAADXZGvcAAAACXBIWXMAAAsSAAALEgHS3X78AAAAK0lEQVQI12NQVVXV0NBQVlYGMoCkrKysHNGAQV1dXUdHR01NDcgA6idJMwCj5Q7+wpqeagAAAABJRU5ErkJggg=='); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px #ffffff;\"\n        alt=\"code_5.png\"\n        title=\"\"\n        src=\"/static/code_5-c383a812e466d5c48235ea70673e1418-3d7b4.png\"\n        srcset=\"/static/code_5-c383a812e466d5c48235ea70673e1418-7e884.png 265w,\n/static/code_5-c383a812e466d5c48235ea70673e1418-9c901.png 530w,\n/static/code_5-c383a812e466d5c48235ea70673e1418-3d7b4.png 800w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>Vue에서는 우리는 <code class=\"language-text\">price</code>나 <code class=\"language-text\">quantity</code>가 업데이트되면 <code class=\"language-text\">total</code>도 업데이트되길 바란다. 바로 다음과 같이 출력되길 원한다:</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/code_6-c2f900a048d57fd007500247c74f85ff-3d7b4.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 9.749999999999998%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAACCAIAAADXZGvcAAAACXBIWXMAAAsSAAALEgHS3X78AAAAKklEQVQI12NQVFRUUVFRV1cHksrKyhoaGnJEAwaIBn19fQ0w0NXVJV4zALUND0/memfaAAAAAElFTkSuQmCC'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px #ffffff;\"\n        alt=\"code_6.png\"\n        title=\"\"\n        src=\"/static/code_6-c2f900a048d57fd007500247c74f85ff-3d7b4.png\"\n        srcset=\"/static/code_6-c2f900a048d57fd007500247c74f85ff-7e884.png 265w,\n/static/code_6-c2f900a048d57fd007500247c74f85ff-9c901.png 530w,\n/static/code_6-c2f900a048d57fd007500247c74f85ff-3d7b4.png 800w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>하지만 불행히도 자바스크립트는 반응형이 아니라 절차적(procedural) 언어라서 우리가 기대하는 기능을 바로 제공하지 않는다. <code class=\"language-text\">total</code> 변수가 반응적이 되도록 만들기 위해서는 자바스크립트를 다른 접근 방식이 필요하다.</p>\n<h2 id=\"️-문제\"><a href=\"#%EF%B8%8F-%EB%AC%B8%EC%A0%9C\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>⚠️ 문제</h2>\n<p><code class=\"language-text\">total</code> 값을 계산하는 코드를 저장해 둘 필요가 있고, <code class=\"language-text\">price</code>나 <code class=\"language-text\">quantity</code> 값이 바뀔 때마다 그 코드를 다시 실행해야 한다.</p>\n<h2 id=\"-해결책\"><a href=\"#-%ED%95%B4%EA%B2%B0%EC%B1%85\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>✅ 해결책</h2>\n<p>우선 우리의 어플리케이션에게 다음의 메시지를 전달할 방법이 필요하다, “내가 실행하려는 코드가 하나 있는데, 이걸 저장해 둬. 나중에 네가 다시 실행할 필요가 생길거야”. 그런 일이 가능하다면 우리는 코드를 한번 실행한 후에, <code class=\"language-text\">price</code>나 <code class=\"language-text\">quantity</code> 값이 바뀔 때 앞서 저장된 코드를 다시 실행하면 된다.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/code_7-cefc1912879706478dc4b5f5ae3efb08-3d7b4.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 34.125%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px #ffffff;\"\n        alt=\"code_7.png\"\n        title=\"\"\n        src=\"/static/code_7-cefc1912879706478dc4b5f5ae3efb08-3d7b4.png\"\n        srcset=\"/static/code_7-cefc1912879706478dc4b5f5ae3efb08-7e884.png 265w,\n/static/code_7-cefc1912879706478dc4b5f5ae3efb08-9c901.png 530w,\n/static/code_7-cefc1912879706478dc4b5f5ae3efb08-3d7b4.png 800w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>이는 함수를 기록하는 방법을 통해 구현할 수 있다.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/code_8-aad6b392a502bb8d92cfd0f11c678457-3d7b4.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 40.875%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px #ffffff;\"\n        alt=\"code_8.png\"\n        title=\"\"\n        src=\"/static/code_8-aad6b392a502bb8d92cfd0f11c678457-3d7b4.png\"\n        srcset=\"/static/code_8-aad6b392a502bb8d92cfd0f11c678457-7e884.png 265w,\n/static/code_8-aad6b392a502bb8d92cfd0f11c678457-9c901.png 530w,\n/static/code_8-aad6b392a502bb8d92cfd0f11c678457-3d7b4.png 800w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p><code class=\"language-text\">target</code> 변수에 익명 함수를 할당해서 재실행할 코드를 저장하고, <code class=\"language-text\">record</code> 함수를 호출했다. ES6 화살표 함수를 사용하면 아래처럼 작성할 수 있다.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/code_9-a5555148497860159b2336c78b634c0d-3d7b4.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 8.25%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAACCAIAAADXZGvcAAAACXBIWXMAAAsSAAALEgHS3X78AAAASklEQVQI11WNSwrAMAgFcxELamL9QPD+p4vETTuLx1MZHKrKzHPOzvdCRCJSYxW9rLUQ8fkz9t6l1a3T3c2sekS0k5m17B8A8JUPD74Q7dGawP4AAAAASUVORK5CYII='); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px #ffffff;\"\n        alt=\"code_9.png\"\n        title=\"\"\n        src=\"/static/code_9-a5555148497860159b2336c78b634c0d-3d7b4.png\"\n        srcset=\"/static/code_9-a5555148497860159b2336c78b634c0d-7e884.png 265w,\n/static/code_9-a5555148497860159b2336c78b634c0d-9c901.png 530w,\n/static/code_9-a5555148497860159b2336c78b634c0d-3d7b4.png 800w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>함수 <code class=\"language-text\">record</code>는 간단히 아래처럼 정의한다.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/code_10-cf989ecca2e06f0fba3d5f8dfb8295b9-3d7b4.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 20.125%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAAECAIAAAABPYjBAAAACXBIWXMAAAsSAAALEgHS3X78AAAAh0lEQVQI1zVOAQ7EIAjzI0IpuiW3Zclti/9/2oHbNY3WUpCiRu/r9km4Oxutmzm4mLUgPEU+05k+CKkSKAqaLzEg0FojCdMM9QgpHKGtI/rNX7JlSVQKYNtxHt/7uu4xxnle+77D1VdTSIQUytkZHz4jnl3CL5YAOS8Yp6oTudlz1KT8+WqRH6kRIB+sHdh8AAAAAElFTkSuQmCC'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px #ffffff;\"\n        alt=\"code_10.png\"\n        title=\"\"\n        src=\"/static/code_10-cf989ecca2e06f0fba3d5f8dfb8295b9-3d7b4.png\"\n        srcset=\"/static/code_10-cf989ecca2e06f0fba3d5f8dfb8295b9-7e884.png 265w,\n/static/code_10-cf989ecca2e06f0fba3d5f8dfb8295b9-9c901.png 530w,\n/static/code_10-cf989ecca2e06f0fba3d5f8dfb8295b9-3d7b4.png 800w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    \n<em>storage 배열에 target 함수를 저장한다.</em></p>\n<p><code class=\"language-text\">target</code> (코드에서는 <code class=\"language-text\">{ total = price * quantity }</code>)을 저장했으니 나중에 다시 실행할 수 있다. 저장한 모든 함수를 실행할 <code class=\"language-text\">replay</code> 함수가 필요할 것이다.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/code_11-7b2d3fbb97ff6ffb9f8b7c74e5e4dcb2-3d7b4.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 14.750000000000002%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAADCAIAAAAcOLh5AAAACXBIWXMAAAsSAAALEgHS3X78AAAAWElEQVQI132NSwoAIQxDvYhtVfqZjzBavP/VpjCr2RhIIAReEgBWOQpbbXxfl5mJSM4ZAL7cKIXVzuHrGdPd11pzzkAwc+9dVTeIhIitViIqRNHpr1g3zy+2tRe6qbGkxgAAAABJRU5ErkJggg=='); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px #ffffff;\"\n        alt=\"code_11.png\"\n        title=\"\"\n        src=\"/static/code_11-7b2d3fbb97ff6ffb9f8b7c74e5e4dcb2-3d7b4.png\"\n        srcset=\"/static/code_11-7b2d3fbb97ff6ffb9f8b7c74e5e4dcb2-7e884.png 265w,\n/static/code_11-7b2d3fbb97ff6ffb9f8b7c74e5e4dcb2-9c901.png 530w,\n/static/code_11-7b2d3fbb97ff6ffb9f8b7c74e5e4dcb2-3d7b4.png 800w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p><code class=\"language-text\">replay</code> 함수를 실행하면 <code class=\"language-text\">storage</code> 배열에 저장한 모든 익명 함수를 실행한다.</p>\n<p>이제 간단히 다음처럼 하면 된다.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/code_12-61e325413c19d8790d98a662df8f541f-3d7b4.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 18.250000000000004%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAAECAIAAAABPYjBAAAACXBIWXMAAAsSAAALEgHS3X78AAAAZUlEQVQI15WOQQ6AIAwEfQkUqbRiQRHwov9/l40P0LjZ7G0yOyRJMUZEnIXNzwxSd+mVk7RW1yfMBN46Dz64D7j0dlxnaZUVItLVF9ZZrUP4gHGZpyTItD3anHOgAB7UjDwa+wbfsGcgphK58PwAAAAASUVORK5CYII='); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px #ffffff;\"\n        alt=\"code_12.png\"\n        title=\"\"\n        src=\"/static/code_12-61e325413c19d8790d98a662df8f541f-3d7b4.png\"\n        srcset=\"/static/code_12-61e325413c19d8790d98a662df8f541f-7e884.png 265w,\n/static/code_12-61e325413c19d8790d98a662df8f541f-9c901.png 530w,\n/static/code_12-61e325413c19d8790d98a662df8f541f-3d7b4.png 800w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>간단하지 않은가? 아래의 코드를 통해 전체 흐름을 다시 한 번 살펴보기 바란다.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/code_13-c8315c0f0db596dee3a4ce00da217b43-3d7b4.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 11.875%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAACCAIAAADXZGvcAAAACXBIWXMAAAsSAAALEgHS3X78AAAAJUlEQVQI12P4////w/ufv3z+BWT8IxEw/P3798f3379//yUDAAAtH3Zq45Xp0gAAAABJRU5ErkJggg=='); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px #ffffff;\"\n        alt=\"code_13.png\"\n        title=\"\"\n        src=\"/static/code_13-c8315c0f0db596dee3a4ce00da217b43-3d7b4.png\"\n        srcset=\"/static/code_13-c8315c0f0db596dee3a4ce00da217b43-7e884.png 265w,\n/static/code_13-c8315c0f0db596dee3a4ce00da217b43-9c901.png 530w,\n/static/code_13-c8315c0f0db596dee3a4ce00da217b43-3d7b4.png 800w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h2 id=\"️-문제-1\"><a href=\"#%EF%B8%8F-%EB%AC%B8%EC%A0%9C-1\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>⚠️ 문제</h2>\n<p>필요한 만큼 target을 기록해 둘 수 있지만, 앱의 확장성을 위해 보다 강력한 솔루션을 만드는 편이 좋을 것이다. target 목록을 유지하면서 우리가 필요할 때 재실행할 수 있도록 알려주는 클래스 같은 것 말이다.</p>\n<h2 id=\"-해결책-의존-클래스\"><a href=\"#-%ED%95%B4%EA%B2%B0%EC%B1%85-%EC%9D%98%EC%A1%B4-%ED%81%B4%EB%9E%98%EC%8A%A4\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>✅ 해결책: 의존 클래스</h2>\n<p>이 문제를 해결할 수 있는 방법 하나는 위의 동작을 표준 프로그래밍 옵저버(observer) 패턴을 구현하는 <strong>의존 클래스</strong>로 캡슐화(encapsulation)하는 것이다.</p>\n<p>그래서, target 같은 종속물(dependencies)을 관리하기 위해 자바스크립트 클래스를 만든다면 아래와 같은 형태가 될 것이다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token keyword\">class</span> <span class=\"token class-name\">Dep</span> <span class=\"token punctuation\">{</span> <span class=\"token comment\">// dependency의 약자</span>\n  <span class=\"token function\">constructor</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>subscribers <span class=\"token operator\">=</span> <span class=\"token punctuation\">[</span><span class=\"token punctuation\">]</span>\n    <span class=\"token comment\">// subscribers는 클래스에 종속되어 있는 target 함수들을 저장하는 배열.</span>\n    <span class=\"token comment\">// notify() 메소드가 호출되면 배열에 저장된 함수들이 실행되어야 한다.</span>\n  <span class=\"token punctuation\">}</span>\n\n  <span class=\"token function\">depend</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span> <span class=\"token comment\">// 앞서 작성한 record 함수를 대체한다.</span>\n    <span class=\"token keyword\">if</span> <span class=\"token punctuation\">(</span>target <span class=\"token operator\">&amp;&amp;</span> <span class=\"token operator\">!</span><span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>subscribers<span class=\"token punctuation\">.</span><span class=\"token function\">includes</span><span class=\"token punctuation\">(</span>target<span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token comment\">// target이 subscribers에 저장되어 있지 않아야 한다.</span>\n      <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>subscribers<span class=\"token punctuation\">.</span><span class=\"token function\">push</span><span class=\"token punctuation\">(</span>target<span class=\"token punctuation\">)</span>\n    <span class=\"token punctuation\">}</span>\n  <span class=\"token punctuation\">}</span>\n\n  <span class=\"token function\">notify</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span> <span class=\"token comment\">// 앞서 작성한 replay 함수를 대체한다.</span>\n    <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>subscribers<span class=\"token punctuation\">.</span><span class=\"token function\">forEach</span><span class=\"token punctuation\">(</span>sub <span class=\"token operator\">=></span> <span class=\"token function\">sub</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span> <span class=\"token comment\">// target 또는 observer를 실행한다.</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<p>이제 <code class=\"language-text\">storage</code> 대신 <code class=\"language-text\">subscribers</code>에 익명 함수를 저장하며, <code class=\"language-text\">record</code> 함수 대신 <code class=\"language-text\">depend</code> 함수를 사용한다. 그리고 <code class=\"language-text\">replay</code> 함수 대신 <code class=\"language-text\">notify</code> 함수를 사용한다. 이 클래스를 작동시켜 보자.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token keyword\">const</span> dep <span class=\"token operator\">=</span> <span class=\"token keyword\">new</span> <span class=\"token class-name\">Dep</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token comment\">// 인스턴스 생성</span>\n\n<span class=\"token keyword\">let</span> price <span class=\"token operator\">=</span> <span class=\"token number\">5</span>\n<span class=\"token keyword\">let</span> quantity <span class=\"token operator\">=</span> <span class=\"token number\">2</span>\n<span class=\"token keyword\">let</span> total <span class=\"token operator\">=</span> <span class=\"token number\">0</span>\n<span class=\"token keyword\">let</span> <span class=\"token function-variable function\">target</span> <span class=\"token operator\">=</span> <span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> <span class=\"token punctuation\">{</span> target <span class=\"token operator\">=</span> price <span class=\"token operator\">*</span> quantity <span class=\"token punctuation\">}</span>\n\ndep<span class=\"token punctuation\">.</span><span class=\"token function\">depend</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token comment\">// subscribers에 target 함수를 추가한다.</span>\n<span class=\"token function\">target</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span>     <span class=\"token comment\">// target 함수를 실행한다.</span>\n\nconsole<span class=\"token punctuation\">.</span><span class=\"token function\">log</span><span class=\"token punctuation\">(</span>total<span class=\"token punctuation\">)</span> <span class=\"token comment\">// => 10 .. 원하는 결과가 맞다.</span>\nprice <span class=\"token operator\">=</span> <span class=\"token number\">20</span>\nconsole<span class=\"token punctuation\">.</span><span class=\"token function\">log</span><span class=\"token punctuation\">(</span>total<span class=\"token punctuation\">)</span> <span class=\"token comment\">// => 10 .. total이 자동으로 업데이트 되지 않았다.</span>\ndep<span class=\"token punctuation\">.</span><span class=\"token function\">notify</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span>       <span class=\"token comment\">// subscribers에 등록된 함수들을 실행한다.</span>\nconsole<span class=\"token punctuation\">.</span><span class=\"token function\">log</span><span class=\"token punctuation\">(</span>total<span class=\"token punctuation\">)</span> <span class=\"token comment\">// => 40 .. 제대로 된 결과를 얻었다.</span></code></pre>\n      </div>\n<p>잘 작동한다. 이제 우리의 코드를 재사용할 수 있을 것 같다. 여전히 이상하게 느껴지는 건 <code class=\"language-text\">target</code>을 등록하고 실행하는 방식이다.</p>\n<h2 id=\"️-문제-2\"><a href=\"#%EF%B8%8F-%EB%AC%B8%EC%A0%9C-2\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>⚠️ 문제</h2>\n<p>개발이 진행되면 변수마다 Dep 클래스를 생성하게 될 것이다. 그리고 업데이트를 감지하는 역할을 하는 익명 함수(target)를 생성하는 과정을 캡슐화하면 좋을 것 같다. <code class=\"language-text\">watcher</code> 함수가 이런 역할을 할 수 있다.</p>\n<p>그러면 아래의 코드를 실행하는 대신:</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/code_14-84e7db824bf2a2faf1d3fb55e7fd364f-3d7b4.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 12.375%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAACCAIAAADXZGvcAAAACXBIWXMAAAsSAAALEgHS3X78AAAARklEQVQI12WISQrAMAgA8xCt1gUEb7nG/78r0kIO7RyGYYaZVdVaa87p7kQkIszcvh9Utd3/+jH6ZmZEdABAL0Q8fuP0hw3/Xw+s7sVvbwAAAABJRU5ErkJggg=='); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px #ffffff;\"\n        alt=\"code_14.png\"\n        title=\"\"\n        src=\"/static/code_14-84e7db824bf2a2faf1d3fb55e7fd364f-3d7b4.png\"\n        srcset=\"/static/code_14-84e7db824bf2a2faf1d3fb55e7fd364f-7e884.png 265w,\n/static/code_14-84e7db824bf2a2faf1d3fb55e7fd364f-9c901.png 530w,\n/static/code_14-84e7db824bf2a2faf1d3fb55e7fd364f-3d7b4.png 800w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>아래처럼 실행할 수 있다:</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/code_15-ae6ed9ac340b6f7e41d00d8bcc0ae6ca-3d7b4.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 15.625%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAADCAIAAAAcOLh5AAAACXBIWXMAAAsSAAALEgHS3X78AAAAUUlEQVQI14WNUQrAIAxDvYd1dW6iIq1+9v4nW+gB3IPAy0dIYObp9N5fJ6UUYyQiyHUkICJiZqoK2XvXWtdaqGOMnzGeW2ulFMjt5JwfB34ef6RoF6paPPzOAAAAAElFTkSuQmCC'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px #ffffff;\"\n        alt=\"code_15.png\"\n        title=\"\"\n        src=\"/static/code_15-ae6ed9ac340b6f7e41d00d8bcc0ae6ca-3d7b4.png\"\n        srcset=\"/static/code_15-ae6ed9ac340b6f7e41d00d8bcc0ae6ca-7e884.png 265w,\n/static/code_15-ae6ed9ac340b6f7e41d00d8bcc0ae6ca-9c901.png 530w,\n/static/code_15-ae6ed9ac340b6f7e41d00d8bcc0ae6ca-3d7b4.png 800w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h2 id=\"-해결책-watcher-함수\"><a href=\"#-%ED%95%B4%EA%B2%B0%EC%B1%85-watcher-%ED%95%A8%EC%88%98\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>✅ 해결책: Watcher 함수</h2>\n<p>Watcher 함수 내부에서 몇가지 간단한 작업을 할 수 있다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token keyword\">function</span> <span class=\"token function\">watcher</span><span class=\"token punctuation\">(</span>myFunc<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n  target <span class=\"token operator\">=</span> myFunc  <span class=\"token comment\">// 현재 처리하고 있는 target으로 myFunc을 설정한다.</span>\n  dep<span class=\"token punctuation\">.</span><span class=\"token function\">depend</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span>     <span class=\"token comment\">// target을 종속물(=종속된 코드)로 추가한다.</span>\n  <span class=\"token function\">target</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span>         <span class=\"token comment\">// target을 실행한다.</span>\n  target <span class=\"token operator\">=</span> <span class=\"token keyword\">null</span>    <span class=\"token comment\">// target을 초기화한다.</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<p>확인할 수 있는 것처럼 <code class=\"language-text\">watcher</code> 함수는 <code class=\"language-text\">myFunc</code>를 인자로 전달받고, 전역 변수인 <code class=\"language-text\">target</code>으로 할당하고, <code class=\"language-text\">dep.depend()</code>를 호출해서 subscriber에 추가하고, <code class=\"language-text\">target</code>을 호출한다. 그리고 <code class=\"language-text\">target</code>를 초기화한다.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/code_16-c37364f594a46add8e2725bf758dfde6-3d7b4.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 18.250000000000004%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAAECAIAAAABPYjBAAAACXBIWXMAAAsSAAALEgHS3X78AAAAVElEQVQI15WOwRGAIAwEqSR84gAxBiMGEPvvS0rA/e/euZwzESEicQIA7z0s48QK31fKR29NVWcrxrgq29O1Wqn2jiEiIYT5YlVG3jdhTEHP8+/yB86WIMwntrd0AAAAAElFTkSuQmCC'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px #ffffff;\"\n        alt=\"code_16.png\"\n        title=\"\"\n        src=\"/static/code_16-c37364f594a46add8e2725bf758dfde6-3d7b4.png\"\n        srcset=\"/static/code_16-c37364f594a46add8e2725bf758dfde6-7e884.png 265w,\n/static/code_16-c37364f594a46add8e2725bf758dfde6-9c901.png 530w,\n/static/code_16-c37364f594a46add8e2725bf758dfde6-3d7b4.png 800w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/code_17-1c1e2183647de8786f7cc4dc3940ccd9-3d7b4.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 13.5%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAADCAIAAAAcOLh5AAAACXBIWXMAAAsSAAALEgHS3X78AAAAL0lEQVQI12P4/Onni2df//37SwZg+PD+x/07n/////+PdMDw6cOPB/c+//tPjtUAMyGxC3Uif5cAAAAASUVORK5CYII='); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px #ffffff;\"\n        alt=\"code_17.png\"\n        title=\"\"\n        src=\"/static/code_17-1c1e2183647de8786f7cc4dc3940ccd9-3d7b4.png\"\n        srcset=\"/static/code_17-1c1e2183647de8786f7cc4dc3940ccd9-7e884.png 265w,\n/static/code_17-1c1e2183647de8786f7cc4dc3940ccd9-9c901.png 530w,\n/static/code_17-1c1e2183647de8786f7cc4dc3940ccd9-3d7b4.png 800w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>당신은 아마 왜 <code class=\"language-text\">target</code>을 함수에 직접 전달하지 않고 전역 변수로 설정했는지 궁금할 것이다. 이렇게 구현한 데에는 이유가 있으며, 이 글의 다 읽으면 그 이유가 명확해지고 좋은 방법이었다는 것을 알게 될 것이다.</p>\n<h2 id=\"️-문제-3\"><a href=\"#%EF%B8%8F-%EB%AC%B8%EC%A0%9C-3\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>⚠️ 문제</h2>\n<p>우리는 하나의 <code class=\"language-text\">Dep</code> 클래스를 가지고 있다. 하지만 우리가 정말로 원하는 것은 변수마다 그만의 Dep 클래스를 가지는 것이다. 더 진행하기 전에 값들을 객체의 속성으로 옮기도록 하자.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/code_18-c1ea54bf6e58da6f00d6019f362556a9-3d7b4.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 7.375000000000001%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAABCAIAAABR8BlyAAAACXBIWXMAAAsSAAALEgHS3X78AAAAJ0lEQVQI12OQk5dXUFRUVlZWVFQEMhSVlBUUFYBISVkFxAYDORwAAOhDB3RNRC9MAAAAAElFTkSuQmCC'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px #ffffff;\"\n        alt=\"code_18.png\"\n        title=\"\"\n        src=\"/static/code_18-c1ea54bf6e58da6f00d6019f362556a9-3d7b4.png\"\n        srcset=\"/static/code_18-c1ea54bf6e58da6f00d6019f362556a9-7e884.png 265w,\n/static/code_18-c1ea54bf6e58da6f00d6019f362556a9-9c901.png 530w,\n/static/code_18-c1ea54bf6e58da6f00d6019f362556a9-3d7b4.png 800w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>각각의 속성(<code class=\"language-text\">price</code>와 <code class=\"language-text\">quantity</code>)이 각각 Dep 클래스를 가지고 있다고 잠시동안 가정해보자.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/code_19-2ca269b3b13a68e2b1662b8d43790508-3d7b4.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 45.24999999999999%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px #ffffff;\"\n        alt=\"code_19.png\"\n        title=\"\"\n        src=\"/static/code_19-2ca269b3b13a68e2b1662b8d43790508-3d7b4.png\"\n        srcset=\"/static/code_19-2ca269b3b13a68e2b1662b8d43790508-7e884.png 265w,\n/static/code_19-2ca269b3b13a68e2b1662b8d43790508-9c901.png 530w,\n/static/code_19-2ca269b3b13a68e2b1662b8d43790508-3d7b4.png 800w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>이제는 아래의 코드를 실행한다.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/code_20-cc5e8a2c9b58def5589ea4da917a720c-3d7b4.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 14.124999999999998%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAADCAIAAAAcOLh5AAAACXBIWXMAAAsSAAALEgHS3X78AAAAXklEQVQI13WNSwoAIQxDvYiOSrH4q5+F9z/aBIXZyGRRkkBfVK11ztlayzmHEIjIbD1bWmvzL4WHtVbvPaUEIyLAMTNYKAEdY5RSvPc3SMUYsXCCtRb+3GOcc1+8l184WBjTmcSbvAAAAABJRU5ErkJggg=='); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px #ffffff;\"\n        alt=\"code_20.png\"\n        title=\"\"\n        src=\"/static/code_20-cc5e8a2c9b58def5589ea4da917a720c-3d7b4.png\"\n        srcset=\"/static/code_20-cc5e8a2c9b58def5589ea4da917a720c-7e884.png 265w,\n/static/code_20-cc5e8a2c9b58def5589ea4da917a720c-9c901.png 530w,\n/static/code_20-cc5e8a2c9b58def5589ea4da917a720c-3d7b4.png 800w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>watcher에 전달된 익명 함수(target) 내부에서 <code class=\"language-text\">data.price</code> 값이 사용되었다. 재실행할 코드에서 <code class=\"language-text\">data.price</code> 값이 사용되고 있으니 <code class=\"language-text\">price</code> 속성에 연결된 Dep 클래스의 subscriber 배열(저장소)에 이 익명 함수를 추가(<code class=\"language-text\">dep.depend()</code>)하고 싶다. 익명 함수 내부에서 <code class=\"language-text\">data.quantity</code> 값도 사용되었으니 <code class=\"language-text\">data.quantity</code>에 연결된 Dep 클래스에도 같은 작업을 하고 싶다.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/code_21-66c2488295ff27c8d97196374f253e78-3d7b4.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 66.5%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px #ffffff;\"\n        alt=\"code_21.png\"\n        title=\"\"\n        src=\"/static/code_21-66c2488295ff27c8d97196374f253e78-3d7b4.png\"\n        srcset=\"/static/code_21-66c2488295ff27c8d97196374f253e78-7e884.png 265w,\n/static/code_21-66c2488295ff27c8d97196374f253e78-9c901.png 530w,\n/static/code_21-66c2488295ff27c8d97196374f253e78-3d7b4.png 800w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>만약 <code class=\"language-text\">data.price</code>를 참조하는 또 다른 익명 함수를 가지고 있다면, 그 함수는 <code class=\"language-text\">price</code> 속성의 Dep 클래스에만 추가하고 싶다.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/code_22-b7577ca7b594d5adc50dcc0725580393-3d7b4.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 28.249999999999996%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px #ffffff;\"\n        alt=\"code_22.png\"\n        title=\"\"\n        src=\"/static/code_22-b7577ca7b594d5adc50dcc0725580393-3d7b4.png\"\n        srcset=\"/static/code_22-b7577ca7b594d5adc50dcc0725580393-7e884.png 265w,\n/static/code_22-b7577ca7b594d5adc50dcc0725580393-9c901.png 530w,\n/static/code_22-b7577ca7b594d5adc50dcc0725580393-3d7b4.png 800w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    \n<em>추가되는 watcher는 속성들 중 하나에만 적용될 것이다</em></p>\n<p>어느 시점에 <code class=\"language-text\">price</code>의 subscriber에 연결된 <code class=\"language-text\">dep.notify()</code>가 호출되어야 할까? 나는 그 순간이 <code class=\"language-text\">price</code> 속성에 값이 할당되었을 때가 되길 바란다. 모든 것이 구현되면 콘솔에서 아래와 같은 출력을 보고 싶다.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/code_23-804c430dc0a43ff7516aed2e67c6e92f-3d7b4.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 15.75%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAADCAIAAAAcOLh5AAAACXBIWXMAAAsSAAALEgHS3X78AAAAWElEQVQI13WNWwrAIAwEPYjRFJ9B8P7Hc2hRSqHzEXY3L1dKMTNVFZEQgvdeXpDID7Rc733O2VrLOddasXaD5i6W1lOB/Ajm3RiD5ZRSjJH/10Y3x34E8wuKahhAq7ZARQAAAABJRU5ErkJggg=='); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px #ffffff;\"\n        alt=\"code_23.png\"\n        title=\"\"\n        src=\"/static/code_23-804c430dc0a43ff7516aed2e67c6e92f-3d7b4.png\"\n        srcset=\"/static/code_23-804c430dc0a43ff7516aed2e67c6e92f-7e884.png 265w,\n/static/code_23-804c430dc0a43ff7516aed2e67c6e92f-9c901.png 530w,\n/static/code_23-804c430dc0a43ff7516aed2e67c6e92f-3d7b4.png 800w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>우리는 data 속성(<code class=\"language-text\">price</code>나 <code class=\"language-text\">quantity</code>)에 원하는 동작을 연결할(hook into) 어떤 방법이 필요하다. 그러면 target 함수 내부에서 어떤 속성이 참조되면 그 속성의 subscriber 배열에 target을 저장할 수 있고, 속성이 변경되면 subscribers 배열에 저장된 함수를 실행하도록 만들 수 있다.</p>\n<h2 id=\"-해결책-objectdefineproperty\"><a href=\"#-%ED%95%B4%EA%B2%B0%EC%B1%85-objectdefineproperty\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>✅ 해결책: Object.defineProperty()</h2>\n<p>ES5에서 제공하는 <a href=\"https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Object/defineProperty\" target=\"_blank\" rel=\"nofollow\">Object.defineProperty</a> 함수에 대해 공부할 필요가 있다. 이 함수는 우리에게 객체 속성의 getter와 setter 함수를 정의할 수 있도록 한다. 이 함수를 Dep 클래스에 적용하기 전에 기본적인 사용법을 살펴보자.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token keyword\">let</span> data <span class=\"token operator\">=</span> <span class=\"token punctuation\">{</span> price<span class=\"token punctuation\">:</span> <span class=\"token number\">5</span><span class=\"token punctuation\">,</span> quantity<span class=\"token punctuation\">:</span> <span class=\"token number\">2</span> <span class=\"token punctuation\">}</span>\n\nObject<span class=\"token punctuation\">.</span><span class=\"token function\">defineProperty</span><span class=\"token punctuation\">(</span>data<span class=\"token punctuation\">,</span> <span class=\"token string\">'price'</span><span class=\"token punctuation\">,</span> <span class=\"token punctuation\">{</span> <span class=\"token comment\">// data의 price 속성에 대해서만 정의한다.</span>\n  <span class=\"token keyword\">get</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span> <span class=\"token comment\">// get 함수 생성. price의 값을 참조하는 역할</span>\n    console<span class=\"token punctuation\">.</span><span class=\"token function\">log</span><span class=\"token punctuation\">(</span><span class=\"token string\">'I was accessed'</span><span class=\"token punctuation\">)</span>\n  <span class=\"token punctuation\">}</span>\n\n  <span class=\"token keyword\">set</span><span class=\"token punctuation\">(</span>newValue<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span> <span class=\"token comment\">// set 함수 생성. price에 새로운 값을 할당하는 역할</span>\n    console<span class=\"token punctuation\">.</span><span class=\"token function\">log</span><span class=\"token punctuation\">(</span><span class=\"token string\">'I was changed'</span><span class=\"token punctuation\">)</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span></code></pre>\n      </div>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/code_24-d9581298d19e1517df61508e9ab12e7a-3d7b4.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 43.24999999999999%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px #ffffff;\"\n        alt=\"code_24.png\"\n        title=\"\"\n        src=\"/static/code_24-d9581298d19e1517df61508e9ab12e7a-3d7b4.png\"\n        srcset=\"/static/code_24-d9581298d19e1517df61508e9ab12e7a-7e884.png 265w,\n/static/code_24-d9581298d19e1517df61508e9ab12e7a-9c901.png 530w,\n/static/code_24-d9581298d19e1517df61508e9ab12e7a-3d7b4.png 800w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/code_25-7a8ba59b4ad0d00ef777c92557ae5a47-3d7b4.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 13.5%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAADCAIAAAAcOLh5AAAACXBIWXMAAAsSAAALEgHS3X78AAAAV0lEQVQI143NSw6AIAwAUe5/QnWhpPwFCiYk7UaQG+DL7EcQcynVWFfr40NwPmhj75h67++KaK3FmEBpxAJqMpeUGXGM0VcEEaWMp5TzD6C2/Zgx85/zB2iVrI5zDl7cAAAAAElFTkSuQmCC'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px #ffffff;\"\n        alt=\"code_25.png\"\n        title=\"\"\n        src=\"/static/code_25-7a8ba59b4ad0d00ef777c92557ae5a47-3d7b4.png\"\n        srcset=\"/static/code_25-7a8ba59b4ad0d00ef777c92557ae5a47-7e884.png 265w,\n/static/code_25-7a8ba59b4ad0d00ef777c92557ae5a47-9c901.png 530w,\n/static/code_25-7a8ba59b4ad0d00ef777c92557ae5a47-3d7b4.png 800w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>보이는 바와 같이 단지 2줄이 로그에 표시된다. 하지만 실제로는 어떤 값도 가져오거나(get) 할당하지(set) 않는다. 우리가 get, set 함수를 새로 덮어씌웠기 때문이다. 그러니 다시 필요한 코드를 추가하도록 하자. <code class=\"language-text\">get()</code> 함수는 값을 반환해야 하고, <code class=\"language-text\">set()</code> 함수는 값을 갱신해야 한다. 그를 위해 <code class=\"language-text\">intervalValue</code>라는 변수에 현재 <code class=\"language-text\">price</code>값을 저장한다.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/code_26-5bd456edee8cade3f4203a4957e44e5e-3d7b4.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 54.125%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px #ffffff;\"\n        alt=\"code_26.png\"\n        title=\"\"\n        src=\"/static/code_26-5bd456edee8cade3f4203a4957e44e5e-3d7b4.png\"\n        srcset=\"/static/code_26-5bd456edee8cade3f4203a4957e44e5e-7e884.png 265w,\n/static/code_26-5bd456edee8cade3f4203a4957e44e5e-9c901.png 530w,\n/static/code_26-5bd456edee8cade3f4203a4957e44e5e-3d7b4.png 800w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    \n<em><code class=\"language-text\">internalValue</code>에서 실제 값을 관리한다.</em></p>\n<p>이제 우리의 get, set 함수는 제대로 동작한다. 콘솔에는 무엇이 출력될까?</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/code_27-2895e65f0cc5606cd667c12fce05b567-3d7b4.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 10.375%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAACCAIAAADXZGvcAAAACXBIWXMAAAsSAAALEgHS3X78AAAAPklEQVQI143LwRGAMAgEwPTfpoCQgw8+yYipIOP+d1QVAJYbcLMJ98ynar0/jJ1V7SJiFmJRs4iYQGZ29zl/a0FzsyhCRy8AAAAASUVORK5CYII='); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px #ffffff;\"\n        alt=\"code_27.png\"\n        title=\"\"\n        src=\"/static/code_27-2895e65f0cc5606cd667c12fce05b567-3d7b4.png\"\n        srcset=\"/static/code_27-2895e65f0cc5606cd667c12fce05b567-7e884.png 265w,\n/static/code_27-2895e65f0cc5606cd667c12fce05b567-9c901.png 530w,\n/static/code_27-2895e65f0cc5606cd667c12fce05b567-3d7b4.png 800w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>이렇게 값을 가져오고(get) 할당할 때(set) 알림을 받을 수 있게 되었다. 그리고 재귀(recursion)를 조금 사용하면 모든 속성에서 이 기능이 작동하도록 할 수 있을 것이다.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/code_28-a0141f4a1a2eb8ab0baa1de132d55222-3d7b4.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 45.62500000000001%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px #ffffff;\"\n        alt=\"code_28.png\"\n        title=\"\"\n        src=\"/static/code_28-a0141f4a1a2eb8ab0baa1de132d55222-3d7b4.png\"\n        srcset=\"/static/code_28-a0141f4a1a2eb8ab0baa1de132d55222-7e884.png 265w,\n/static/code_28-a0141f4a1a2eb8ab0baa1de132d55222-9c901.png 530w,\n/static/code_28-a0141f4a1a2eb8ab0baa1de132d55222-3d7b4.png 800w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    \n<em>Object.keys 함수를 사용해서 data 객체에 존재하는 모든 속성에 같은 기능을 적용했다</em></p>\n<p>이제 모든 속성이 getter와 setter 함수를 가지게 된 것을 콘솔에서 확인할 수 있다.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/code_29-35e2334810af4a78b56a6f766a6fc31f-3d7b4.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 13.5%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAADCAIAAAAcOLh5AAAACXBIWXMAAAsSAAALEgHS3X78AAAASklEQVQI142OwRHAIAgE6b/LRAE5ia/oy4mhBO6/u0eGzixA7+6Ar7XOOV9uJKLXXURbKTUs4XjnTOJkQK2satosyGeMOLH3zvR/2qGt0WReFAoAAAAASUVORK5CYII='); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px #ffffff;\"\n        alt=\"code_29.png\"\n        title=\"\"\n        src=\"/static/code_29-35e2334810af4a78b56a6f766a6fc31f-3d7b4.png\"\n        srcset=\"/static/code_29-35e2334810af4a78b56a6f766a6fc31f-7e884.png 265w,\n/static/code_29-35e2334810af4a78b56a6f766a6fc31f-9c901.png 530w,\n/static/code_29-35e2334810af4a78b56a6f766a6fc31f-3d7b4.png 800w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h2 id=\"-지금까지의-아이디어를-합치기\"><a href=\"#-%EC%A7%80%EA%B8%88%EA%B9%8C%EC%A7%80%EC%9D%98-%EC%95%84%EC%9D%B4%EB%94%94%EC%96%B4%EB%A5%BC-%ED%95%A9%EC%B9%98%EA%B8%B0\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>🛠 지금까지의 아이디어를 합치기</h2>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/code_30-67ea093e72307a91cb8549f03aa815d5-3d7b4.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 7.250000000000001%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAABCAIAAABR8BlyAAAACXBIWXMAAAsSAAALEgHS3X78AAAAHElEQVQI12OQk5NTVlZWBAMgQ0lJCZkNIeVwAADrqAeNnC3t6QAAAABJRU5ErkJggg=='); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px #ffffff;\"\n        alt=\"code_30.png\"\n        title=\"\"\n        src=\"/static/code_30-67ea093e72307a91cb8549f03aa815d5-3d7b4.png\"\n        srcset=\"/static/code_30-67ea093e72307a91cb8549f03aa815d5-7e884.png 265w,\n/static/code_30-67ea093e72307a91cb8549f03aa815d5-9c901.png 530w,\n/static/code_30-67ea093e72307a91cb8549f03aa815d5-3d7b4.png 800w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>위의 코드처럼 <code class=\"language-text\">price</code>의 값을 사용(<strong>get</strong>)한다면, 위의 코드(target)가 <code class=\"language-text\">price</code> 값에 의존하고 있다는 사실을 기록해두고 싶다. 그렇게 해 둔다면 <code class=\"language-text\">price</code> 값의 변경이나 새로운 값의 할당이 저장해둔 코드를 재실행할 수 있는 계기(trigger)가 되도록 만들 수 있다. 우리의 어플리케이션은 어떤 코드가 <code class=\"language-text\">price</code>에 의존하고 있는지 알고 있기 때문이다. 따라서 getter와 setter를 아래처럼 동작하도록 생각할 수 있다.</p>\n<p><strong>Get</strong> => 이 익명 함수를 기억해라, 값이 변경될 때 다시 실행할 것이다.</p>\n<p><strong>Set</strong> => 저장된 익명 함수를 실행해라. 값은 방금 변경되었다.</p>\n<p>Dep Class의 경우에는</p>\n<p><strong>Price가 사용되었다 (get)</strong> => <code class=\"language-text\">dep.depend()</code> 함수를 호출해서 현재 <code class=\"language-text\">target</code>을 저장해라.</p>\n<p><strong>Price가 할당되었다 (set)</strong> => price의 <code class=\"language-text\">dep.notify()</code>를 해서 모든 <code class=\"language-text\">targets</code>를 재실행해라.</p>\n<p>이 두가지 아이디어를 조합한 최종 코드를 살펴보자.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token keyword\">let</span> data <span class=\"token operator\">=</span> <span class=\"token punctuation\">{</span> price<span class=\"token punctuation\">:</span> <span class=\"token number\">5</span><span class=\"token punctuation\">,</span> quantity<span class=\"token punctuation\">:</span> <span class=\"token number\">2</span> <span class=\"token punctuation\">}</span>\n<span class=\"token keyword\">let</span> target <span class=\"token operator\">=</span> <span class=\"token keyword\">null</span>\n\n<span class=\"token comment\">// 앞에서 선언한 Dep 클래스와 동일하다.</span>\n<span class=\"token keyword\">class</span> <span class=\"token class-name\">Dep</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token function\">constructor</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>subscribers <span class=\"token operator\">=</span> <span class=\"token punctuation\">[</span><span class=\"token punctuation\">]</span>\n  <span class=\"token punctuation\">}</span>\n\n  <span class=\"token function\">depend</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token keyword\">if</span> <span class=\"token punctuation\">(</span>target <span class=\"token operator\">&amp;&amp;</span> <span class=\"token operator\">!</span><span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>subscribers<span class=\"token punctuation\">.</span><span class=\"token function\">includes</span><span class=\"token punctuation\">(</span>target<span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n      <span class=\"token comment\">// target이 존재하며 subscribers에 포함되어 있지 않을 때만 추가한다.</span>\n      <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>subscribers<span class=\"token punctuation\">.</span><span class=\"token function\">push</span><span class=\"token punctuation\">(</span>target<span class=\"token punctuation\">)</span>\n    <span class=\"token punctuation\">}</span>\n  <span class=\"token punctuation\">}</span>\n\n  <span class=\"token function\">notify</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>subscribers<span class=\"token punctuation\">.</span><span class=\"token function\">forEach</span><span class=\"token punctuation\">(</span>sub <span class=\"token operator\">=></span> <span class=\"token function\">sub</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token comment\">// data 객체가 가지고 있는 모든 속성에 적용한다</span>\nObject<span class=\"token punctuation\">.</span><span class=\"token function\">keys</span><span class=\"token punctuation\">(</span>data<span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span><span class=\"token function\">forEach</span><span class=\"token punctuation\">(</span>key <span class=\"token operator\">=></span> <span class=\"token punctuation\">{</span>\n  <span class=\"token keyword\">let</span> internalValue <span class=\"token operator\">=</span> data<span class=\"token punctuation\">[</span>key<span class=\"token punctuation\">]</span>\n\n  <span class=\"token comment\">// 각각의 속성은 자기만의 Dep 클래스 인스턴스를 가진다.</span>\n  <span class=\"token keyword\">const</span> dep <span class=\"token operator\">=</span> <span class=\"token keyword\">new</span> <span class=\"token class-name\">Dep</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span>\n\n  <span class=\"token comment\">// data 객체 속성의 getter, setter를 재설정한다.</span>\n  Object<span class=\"token punctuation\">.</span><span class=\"token function\">defineProperty</span><span class=\"token punctuation\">(</span>data<span class=\"token punctuation\">,</span> key<span class=\"token punctuation\">,</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token keyword\">get</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n      <span class=\"token comment\">// getter가 호출된 시점의 target을 기억하도록 한다.</span>\n      dep<span class=\"token punctuation\">.</span><span class=\"token function\">depend</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span>\n      <span class=\"token keyword\">return</span> internalValue\n    <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span>\n    <span class=\"token keyword\">set</span><span class=\"token punctuation\">(</span>newVal<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n      internalValue <span class=\"token operator\">=</span> newVal\n      dep<span class=\"token punctuation\">.</span><span class=\"token function\">notify</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token comment\">// 저장된 함수(target)를 재실행</span>\n    <span class=\"token punctuation\">}</span>\n  <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span>\n\n<span class=\"token comment\">// watcher는 더 이상 dep.depend를 호출하지 않는다.</span>\n<span class=\"token comment\">// 왜냐하면 get 메소드 안에서 자동으로 호출되기 때문이다.</span>\n<span class=\"token keyword\">function</span> <span class=\"token function\">watcher</span><span class=\"token punctuation\">(</span>myFunc<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n  target <span class=\"token operator\">=</span> myFunc\n  <span class=\"token function\">target</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token comment\">// 할당된 코드는 무조건 1번은 실행되어야 한다.</span>\n  target <span class=\"token operator\">=</span> <span class=\"token keyword\">null</span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token comment\">// watcher에 전달된 이 익명함수에서 price와 quantity의 getter 함수가 실행된다.</span>\n<span class=\"token comment\">// watcher가 실행된 시점에서 전역 변수 target에는 watcher의 인자로 전달된 익명 함수가 할당된다</span>\n<span class=\"token function\">watcher</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> <span class=\"token punctuation\">{</span>\n  data<span class=\"token punctuation\">.</span>total <span class=\"token operator\">=</span> data<span class=\"token punctuation\">.</span>price <span class=\"token operator\">*</span> data<span class=\"token punctuation\">.</span>quantity\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span></code></pre>\n      </div>\n<p>이제 코드를 실행하면 콘솔에 무엇이 출력되는지 확인해보자.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/code_32-45e6eddd235d3d9aa428c53f84d96a2e-3d7b4.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 33.875%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAAHCAIAAACHqfpvAAAACXBIWXMAAAsSAAALEgHS3X78AAAAYUlEQVQY052PSxaAIAwDuf81RX2iYoFCC/KRK9TJKoukjTLmBoBEPMboQtSqrxACTvnSmjB8HoARmbi+XXx53x7EwJz/vG2Mdc6DcxFTyVXUoAD8hIimEW/WiyWKOZch5wNh052XT0mv4QAAAABJRU5ErkJggg=='); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px #ffffff;\"\n        alt=\"code_32.png\"\n        title=\"\"\n        src=\"/static/code_32-45e6eddd235d3d9aa428c53f84d96a2e-3d7b4.png\"\n        srcset=\"/static/code_32-45e6eddd235d3d9aa428c53f84d96a2e-7e884.png 265w,\n/static/code_32-45e6eddd235d3d9aa428c53f84d96a2e-9c901.png 530w,\n/static/code_32-45e6eddd235d3d9aa428c53f84d96a2e-3d7b4.png 800w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>우리가 하고 싶었던 것과 정확히 일치한다! <code class=\"language-text\">price</code>와 <code class=\"language-text\">quantity</code> 모두 확실히 반응한다! watcher 함수에 전달한 익명 함수는 <code class=\"language-text\">price</code> 또는 <code class=\"language-text\">quantity</code> 값이 변경될 때마다 재실행되고 있다.</p>\n<p>Vue 문서에서 가져온 이 그림은 이제 이해가 될 것이다.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/code_33-d3884f056f15048109d21cc663ecc102-3d7b4.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 62.5%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px #ffffff;\"\n        alt=\"code_33.png\"\n        title=\"\"\n        src=\"/static/code_33-d3884f056f15048109d21cc663ecc102-3d7b4.png\"\n        srcset=\"/static/code_33-d3884f056f15048109d21cc663ecc102-7e884.png 265w,\n/static/code_33-d3884f056f15048109d21cc663ecc102-9c901.png 530w,\n/static/code_33-d3884f056f15048109d21cc663ecc102-3d7b4.png 800w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>Data와 getter, setter를 가지고 있는 아름다운 보라색 동그라미가 보이는가? 아마 익숙하게 보일 것이다! 모든 컴포넌트 인스턴스는 getter를 통해 종속물을 수집하는(붉은 점선) <code class=\"language-text\">watcher</code> 인스턴스(파란색)를 가지고 있다. setter가 호출되면 Data는 watcher에게 <strong>알림(Notify)</strong>을 보내서 컴포넌트 re-render로 이어지게 한다. 아래는 내가 주석을 추가한 그림이다.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/code_34-5025361c70d548d6b4618579754845c6-3d7b4.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 50.625%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px #ffffff;\"\n        alt=\"code_34.png\"\n        title=\"\"\n        src=\"/static/code_34-5025361c70d548d6b4618579754845c6-3d7b4.png\"\n        srcset=\"/static/code_34-5025361c70d548d6b4618579754845c6-7e884.png 265w,\n/static/code_34-5025361c70d548d6b4618579754845c6-9c901.png 530w,\n/static/code_34-5025361c70d548d6b4618579754845c6-3d7b4.png 800w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>어떤가, 이쪽이 우리가 지금까지 살펴본 코드를 떠올리게 하면서 더 잘 와닿지 않는가??</p>\n<p>사실 Vue의 반응성 엔진은 이것보다 훨씬 더 복잡하다. 하지만 당신은 이제 기초를 알게 되었다.</p>\n<h2 id=\"-지금까지-학습한-내용\"><a href=\"#-%EC%A7%80%EA%B8%88%EA%B9%8C%EC%A7%80-%ED%95%99%EC%8A%B5%ED%95%9C-%EB%82%B4%EC%9A%A9\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>⏪ 지금까지 학습한 내용</h2>\n<ul>\n<li>종속물(종속된 코드, dependencies)를 수집하는(<code class=\"language-text\">depend</code>) <strong>Dep class</strong>를 어떻게 선언하고, 어떻게 의존 코드를 재실행하는지(<code class=\"language-text\">notify</code>)</li>\n<li>종속물로 추가될 수 있는 실행 코드(<code class=\"language-text\">target</code>)를 관리하기 위해 <strong>watcher</strong>를 어떻게 생성하는지.</li>\n<li>getter와 setter를 생성하기 위해  <strong>Object.defineProperty()</strong>를 어떻게 사용하는지</li>\n</ul>\n<h2 id=\"이-다음은\"><a href=\"#%EC%9D%B4-%EB%8B%A4%EC%9D%8C%EC%9D%80\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>이 다음은?</h2>\n<p>이 글을 통해 배운 내용에 흥미를 느꼈다면 다음 단계는 <a href=\"https://www.vuemastery.com/courses/advanced-components/evan-you-on-proxies/\" target=\"_blank\" rel=\"nofollow\">Reactivity with Proxies</a>다. 그리고 내가 Vue 개발자인 Evan You와 함께 이 주제에 대해서 이야기하는 <a href=\"https://www.vuemastery.com/courses/advanced-components/evan-you-on-proxies/\" target=\"_blank\" rel=\"nofollow\">무료 동영상</a>도 VueMastery.com에서 꼭 확인해 보길 바란다.</p>","timeToRead":13,"fields":{"tagSlugs":["/tag/javascript","/tag/vue.js","/tag/번역"]},"frontmatter":{"title":"[번역] 자바스크립트 반응성(Reactivity)에 대한 가장 좋은 설명","subTitle":"SPA 프레임워크에서 컴포넌트의 상태가 변경되면 렌더링이 다시 실행되는 것처럼, 객체의 속성이 변경될 때 어떤 코드가 자동으로 실행되게 한다면 그 어플리케이션은 반응적이라고 할 수 있다. 이를 구현하기 위해서 `Object.defineProperty` 함수를 사용해 getter, setter 함수를 다시 할당하는 방법을 설명하는 글이다.","tags":["JavaScript","Vue.js","번역"],"date":"2018-09-12","mainImageAlt":"","description":"","mainImage":{"childImageSharp":{"sizes":{"aspectRatio":3.3333333333333335,"base64":"data:image/jpeg;base64,","sizes":"(max-width: 1280px) 100vw, 1280px","src":"/static/main-308ed66e1980d51d8f248abb0996cab7-6ff54.jpeg","srcSet":"/static/main-308ed66e1980d51d8f248abb0996cab7-3c4d0.jpeg 320w,\n/static/main-308ed66e1980d51d8f248abb0996cab7-e154f.jpeg 640w,\n/static/main-308ed66e1980d51d8f248abb0996cab7-6ff54.jpeg 1280w,\n/static/main-308ed66e1980d51d8f248abb0996cab7-60435.jpeg 1400w"}}}}}},"pathContext":{"slug":"/posts/2018-09-12-javascript-reactivity/"}}

/***/ })

});


